@tailwind base;
@tailwind components;
@tailwind utilities;

.app {
  height: 100vh;
  overflow: hidden;
}

.box {
  border-radius: 1.5rem;
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin: 0.5rem;
}

.left-panel {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.chat-panel {
  width: 60%;
  padding: 0.5rem;
}

.right-panel {
  width: 20%;
}

.chat-title {
  font-size: 1.25rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.chat-window {
  height: calc(100vh - 180px);
  overflow-y: auto;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
}

.chat-message {
  margin-bottom: 0.5rem;
  display: flex;
  align-items: flex-start;
  gap: 0.5rem;
}

.chat-message.user {
  justify-content: flex-end;
  text-align: right;
}

.chat-message.bot {
  justify-content: flex-start;
}

.chat-input {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 0.5rem;
}

.chat-input input {
  width: 100%;
  padding: 0.5rem;
  border-radius: 0.5rem;
  background-color: #1f2937;
  color: white;
}

.btn-box {
  @apply flex flex-col items-center justify-center text-white font-semibold p-3 rounded-lg bg-gray-700 hover:bg-gray-600;
}

.transcription-box {
  height: 200px;
}

.scrollable-box {
  width: 100%;
  height: 100%;
  background-color: #1f2937;
  border: 1px solid #334155;
  border-radius: 0.5rem;
  color: #fff;
  padding: 0.5rem;
  overflow-y: auto;
}

.waveform-container {
  height: 80px;
  background-color: #1e293b;
  border-radius: 0.5rem;
}
